html, img {box-sizing: border-box;}
*, *:before, *:after {box-sizing: inherit;}

body {
  margin: 0;        
  height: 100%;
  background-color: black;
}
a { 
  &:link {text-decoration: none !important;}
  &:visited {color: inherit !important;}
  &:active {color: inherit !important;}
}

header {
  padding: 3vh 2vw 3vh 0;
  height: 11vh;
  display: flex;
  justify-content: space-between;
  align-items: center;

  nav {
    display: flex;
    align-items: center;
    h1 {
      margin: 0 3vw;
    }
    * {
      margin: 0 .5vw;
    }
  }

  &.light {
    background-color: $white1;
    color: $dark1;
    button{
      &:hover {
        background-color: $dark1;
        color: $white1;
      }
    }
  }
  &.dark {
    background-color: $dark1;
    color: $white1;
    button {
      background-color: $dark1;
      color: $white1;
      border-color: black;
      &:hover {
        background-color: $white1;
        color: $dark1;
      }
    }
  }
}

main {
  padding: 0 11vh;
  min-height: 89vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  background-image: linear-gradient($linear-dark, $linear-light), url("../assets/images/bg-leaves.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
}

.hero {
  padding: 9vh 22vh;
  width: 66vw;
  border-radius: 3vh;
  background-color: $opac-white;
  color: $dark1;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  h1 {
    font-size: 2.3rem;
  }
  h2 {
    font-size: 1.8rem;
  }
  p {
    font-size: 1rem;
    width: 85%;
  }
  button {
    margin-top: 3vh;
  }

  &.dark {
    background-color: $opac-dark;
    color: $white1;
  }
}


.projects {
  width: 66vw;
  display: flex;
  justify-content: space-between;
}

.project {
  width: 25%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  background-color: $opac-white;
  border-radius: 3vh;
  &__title {
    margin: 2vh 0;
  }
  &__picture {
    height: 22vh;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__description {
    margin: 1vh 0;
    text-align: center;
  }
  &__content {
    margin-top: 3vh;
  }
  button {
    margin-bottom: 2vh;
  }

  &-solo {
    width: 100%;
    background-color: transparent;
  }

  &.light {
    background-color: $opac-white;
    color: $dark1;
    button{
      &:hover {
        background-color: $dark1;
        color: $white1;
      }
    }
  }
  &.dark {
    background-color: $opac-dark;
    color: $white1;
    button {
      background-color: $dark1;
      color: $white1;
      border-color: black;
      &:hover {
        background-color: $white1;
        color: $dark1;
      }
    }
  }
}

button {
  padding: 1vh 3vw;
  border-radius: 11px;
}